body, div {
  margin: 0;
  padding: 0;
}

.page {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  align-self: center;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  transform-origin: bottom;
  animation: infinite spin;
  position: absolute;
}

.stopwatch__hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  top: 20vmin;
}

.stopwatch__hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-duration: 60s;
  animation-timing-function: linear;
  top: 2vmin;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
